.SearchResults {
  width: 50%;
  height: 600px;
  overflow-y: scroll;
  padding: 0.88rem;
  background-color: transparent;
  box-shadow: #3871927c 0px 7px 29px 0px;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}
