body,
html,
#root {
  height: auto;
/*   background: linear-gradient(95deg, hotpink, rgb(14, 224, 161)); */
  background: linear-gradient(
      217deg,
      rgba(154, 35, 170, 0.616),
      rgba(255, 0, 0, 0) 70.71%
    ),
    linear-gradient(127deg, rgba(16, 211, 162, 0.8), rgba(0, 255, 0, 0) 70.71%),
    linear-gradient(
      336deg,
      rgba(255, 105, 180, 0.596),
      rgba(0, 0, 255, 0) 70.71%
    );
  background-size: cover;
  background-repeat: no-repeat;
}

html {
  font-size: 18px;
}


h1 {
  text-align: center;
  font-family: "Fjalla One", "Poppins", sans-serif;
  font-size: 2rem;
  padding-top: 10rem;
  margin-top: 0;
  margin-bottom: 0;
  text-shadow: 1px 1px rgba(119, 118, 118, 0.39);
}
@media only screen and (min-width: 600px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.55rem;
  text-align: center;
}

.App {
  height: fit-content;
  padding: 0 7% 10% 7%;
  font-family: "Work Sans", sans-serif;
  font-weight: 500;
  color: #ebe4f5;
}
@media only screen and (min-width: 800px) {
  .App {
    padding: 0 17% 10% 17%;
  }
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}
